import "./App.scss";
import React from "react";
import TextFrame from "./components/TextFrame/TextFrame";
import ScrollBar from "./components/ScrollBar/ScrollBar";
import EmotionsMap from "./components/EmotionsMap/EmotionsMap";
import GeoMap from "./components/GeoMap/GeoMap";
import Players from "./components/Players/Players";
import AutoComplete from "./components/AutoComplete/AutoComplete";

const chaptersData = require("./data/chapters_new.json").chapters;
const episodes = require("./data/episodes.json").episodes;
const searchWords = require("./data/search_list.json").words;
const defs = require("./data/Defs.json");

function produceEmotionsString(emotions) {
  var emotionsString = "";
  var i = 0;
  defs.emotions.map(({ type, hebrewLabel }) => {
    const value = emotions.find((emotion) => emotion.type === type).value;
    if (value > 0) {
      emotionsString += hebrewLabel + ": " + parseInt(value * 100) + "%, ";
      i++;
      if (i % 2 === 0) {
        emotionsString += "\n";
      }
    }
  });
  return i % 2 == 0 ? emotionsString.slice(0, -3) : emotionsString.slice(0, -2);
}

const hoverDivs = chaptersData.map(
  ({ date, place, header, headerEmotions }) => {
    return (
      <div>
        <div className="hoverDate" id="hoverDate">
          {date.day + "." + date.month + "." + date.year}
        </div>
        <div className="hoverPlace" id="hoverPlace">
          {place.city + ", " + place.country}
        </div>
        <div className="hoverHeader" id="hoverHeader">
          {header}
        </div>
        <div className="hoverEmotions" id="hoverEmotions">
          {produceEmotionsString(headerEmotions)}
        </div>
      </div>
    );
  }
);

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      searchObjects: {},
      searchIndices: [],
      chaptersTops: [],
      activeId: 0,
      hoverId: -1,
      prevHoverId: 0,
      scrollPos: 0,
      heightFract: 0,
      infoState: "fadeIn",
      wideScreen: false,
      playlist: false,
      line: [],
      episode: 0,
      episodeBounds: [0, 97],
    };
  }

  componentDidMount() {
    const chapters = Array.prototype.slice.call(
      document.getElementsByClassName("chapter")
    );
    const totalHeight = document.getElementById("chapter").offsetHeight;
    const chaptersTops = chapters.map((chapter) => {
      return (chapter.offsetTop / totalHeight) * 100;
    });
    this.setState({ chaptersTops: chaptersTops });
  }

  handleSearch = (string) => {
    var searchObjects = {};
    var searchIndices = [];
    const words = searchWords[string];
    if (words) {
      searchObjects = words;
      searchIndices = Object.keys(words);
    }
    this.setState({
      searchObjects: searchObjects,
      searchIndices: searchIndices,
    });
  };

  setActive = (id, line = true) => {
    if (this.state.activeId !== id) {
      if (!line) {
        this.setState({
          activeId: id,
        });
        return;
      }
      if (this.state.episode > -1) {
        this.setState({
          activeId: id,
          episode: -1,
          episodeBounds: [],
        });
      } else {
        this.setState({
          activeId: id,
          line: [this.state.activeId, id],
        });
        setTimeout(
          function () {
            const connector = document.getElementById("emotionsConnector");
            if (connector) {
              connector.addEventListener("animationend", () => {
                this.setState({ line: [] });
              });
            }
          }.bind(this),
          50
        );
      }
    }
  };

  setHover = (id) => {
    if (id > -1) {
      this.setState({
        hoverId: id,
      });
    } else {
      this.setState({
        prevHoverId:
          this.state.hoverId > -1 ? this.state.hoverId : this.state.prevHoverId,
        hoverId: id,
      });
    }
  };

  setScrollPos = (scrollPos) => {
    this.setState({
      scrollPos: scrollPos,
    });
  };

  setHeightFract = (heightFract) => {
    this.setState({
      heightFract: heightFract,
    });
  };

  setInfoState = (value) => {
    if (value) {
      this.setState({
        infoState: "fadeIn",
      });
    } else {
      this.setState({
        infoState: "fadeOut",
      });
      setTimeout(
        function () {
          this.setState({ infoState: "" });
        }.bind(this),
        300
      );
    }
  };

  setWideScreen = () => {
    this.setState({
      wideScreen: !this.state.wideScreen,
    });
  };

  setPlaylist = (value) => {
    this.setState({
      playlist: value,
    });
  };

  setEpisode = (index) => {
    if (index !== this.state.episode) {
      this.setState({
        episode: index,
        episodeBounds:
          index > -1
            ? [episodes[index].startEpisode, episodes[index].endEpisode]
            : [],
      });
    }
  };

  render() {
    const infoIcon = this.state.infoState ? "exitImg" : "infoImg";
    return (
      <div>
        <div
          className={infoIcon + " infoIcon"}
          onClick={() => this.setInfoState(!this.state.infoState)}
        />
        <div className="GraphFrame map">
          <GeoMap
            activeId={this.state.activeId}
            setActive={this.setActive}
            setHover={this.setHover}
            hoverId={this.state.hoverId}
            searchIndices={this.state.searchIndices}
            infoState={this.state.infoState}
            line={this.state.line}
            episodeBounds={this.state.episodeBounds}
          />
        </div>
        <div className="EmotionsFrame">
          <AutoComplete onSearch={this.handleSearch} />
          <EmotionsMap
            activeId={this.state.activeId}
            setActive={this.setActive}
            setHover={this.setHover}
            hoverId={this.state.hoverId}
            searchIndices={this.state.searchIndices}
            infoState={this.state.infoState}
            line={this.state.line}
            episodeBounds={this.state.episodeBounds}
          />
        </div>
        <div
          className="frontBackground"
          style={{ right: this.state.wideScreen ? "75%" : "100%" }}
        ></div>
        <div
          className="textFrame"
          style={{ right: this.state.wideScreen ? "25%" : 0 }}
        >
          <TextFrame
            onSearch={this.handleSearch}
            setActive={this.setActive}
            activeId={this.state.activeId}
            hoverId={this.state.hoverId}
            searchIndices={this.state.searchObjects}
            setScrollPos={this.setScrollPos}
            heightFract={this.state.heightFract}
            infoState={this.state.infoState}
            setWideScreen={this.setWideScreen}
            wideScreen={this.state.wideScreen}
            setPlaylist={this.setPlaylist}
            episode={this.state.episode}
            setEpisode={this.setEpisode}
          />
          {this.createHoverBoxScrollBar()}
        </div>
        <div
          className="scrollBarContainer"
          style={{ left: this.state.wideScreen ? "25%" : "50%" }}
        >
          <ScrollBar
            hoverId={this.state.hoverId}
            scrollPos={this.state.scrollPos}
            setScrollPos={this.setScrollPos}
            searchIndices={this.state.searchIndices}
            setHeightFract={this.setHeightFract}
            setHover={this.setHover}
            infoState={this.state.infoState}
            playlist={this.state.playlist}
            setEpisode={this.setEpisode}
            setActive={this.setActive}
          />
        </div>
        <Players
          activeId={this.state.activeId}
          setActive={this.setActive}
          setHover={this.setHover}
          playlist={this.state.playlist}
          setPlaylist={this.setPlaylist}
          infoState={this.state.infoState}
        />
      </div>
    );
  }

  createHoverBoxScrollBar() {
    const hoverIndex =
      this.state.hoverId == -1 ? this.state.prevHoverId : this.state.hoverId;
    const hoverTop = this.state.chaptersTops[hoverIndex];
    const styleParameter = hoverTop > 50 ? "bottom" : "top";
    const offset = hoverTop > 50 ? 100 - hoverTop : hoverTop;
    return (
      <div
        className={"hoverBox hoverBoxScrollBar"}
        id={"hoverBoxScrollBar"}
        style={{
          left: this.state.hoverId > -1 ? -200 + "px" : 0,
          [styleParameter]: offset + "%",
        }}
      >
        {hoverDivs[hoverIndex]}
      </div>
    );
  }
}

export default App;
