import React from "react";
import "./AutoComplete.scss";
import Autosuggest from "react-autosuggest";

const search_list = require("../../data/search_list.json").words;
const search_words = Object.keys(search_list).sort();
function getSuggestionValue(suggestion) {
  return suggestion;
}

function renderSuggestion(suggestion) {
  return <span>{suggestion}</span>;
}

class AutoComplete extends React.Component {
  constructor() {
    super();

    this.state = {
      value: "",
      suggestions: [],
      activeIcon: false,
    };
  }

  getSuggestions(value) {
    if (!value) {
      return search_words;
    }
    const regex = new RegExp("^" + value, "i");
    return search_words.filter((searchWord) => regex.test(searchWord)).sort();
  }

  onChange = (event, { newValue, method }) => {
    this.setState({
      value: newValue,
      activeIcon: false,
    });
  };

  handleKeyPress = (event) => {
    if (event.key === "Enter") {
      this.props.onSearch(this.state.text);
      this.setState({
        activeIcon: true,
      });
    }
  };

  onSuggestionsFetchRequested = ({ value }) => {
    this.setState({
      suggestions: this.getSuggestions(value),
    });
  };

  onSuggestionsClearRequested = () => {
    this.setState({
      suggestions: [],
    });
  };

  onSuggestionSelected = (event, { suggestion }) => {
    this.props.onSearch(getSuggestionValue(suggestion));
    this.setState({
      activeIcon: true,
    });
  };

  sholeRenderSuggestions = () => {
    return true;
  };

  clearSearch = () => {
    this.props.onSearch("");
    this.setState({
      value: "",
      suggestions: [],
    });
  };

  render() {
    const { value, suggestions } = this.state;
    const inputProps = {
      placeholder: "חיפוש",
      value,
      onChange: this.onChange,
    };

    const searchClass = this.state.activeIcon ? " active" : "";

    return (
      <div className="autoCompleteContainer">
        <div
          className={"clearIcon"}
          onClick={this.clearSearch}
          style={{ opacity: this.state.value ? 1 : 0 }}
        />
        <Autosuggest
          suggestions={suggestions}
          onSuggestionsFetchRequested={this.onSuggestionsFetchRequested}
          onSuggestionsClearRequested={this.onSuggestionsClearRequested}
          getSuggestionValue={getSuggestionValue}
          shouldRenderSuggestions={this.sholeRenderSuggestions}
          renderSuggestion={renderSuggestion}
          onSuggestionSelected={this.onSuggestionSelected}
          highlightFirstSuggestion={true}
          inputProps={inputProps}
        />
        <div className={"searchIcon" + searchClass} />
      </div>
    );
  }
}

export default AutoComplete;
