import React from "react";
import "./ScrollBar.scss";

const months = [
  "ינואר",
  "פברואר",
  "מרץ",
  "אפריל",
  "מאי",
  "יוני",
  "יולי",
  "אוגוסט",
  "ספטמבר",
  "אוקטובר",
  "נובמבר",
  "דצמבר",
];

const episodesData = require("../../data/episodes.json").episodes;
const chapters = require("../../data/chapters_new.json").chapters;

class ScrollBar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      markerPos: -100,
      chaptersTops: [],
      hoverMouseId: -1,
      prevSearches: [],
      currentSearches: [],
      mousePos: 0,
      episodesMarkers: null,
      episodeTitle: "",
    };
  }

  episodeClicked = (e, i, start) => {
    e.stopPropagation();
    this.props.setActive(start, false);
    setTimeout(
      function () {
        this.props.setEpisode(i);
      }.bind(this),
      50
    );
    setTimeout(
      function () {
        this.props.setEpisode(i);
      }.bind(this),
      100
    );
  };

  componentDidMount() {
    const chapters = Array.prototype.slice.call(
      document.getElementsByClassName("chapter")
    );
    const totalHeight = document.getElementById("chapter").offsetHeight;
    const chaptersTops = chapters.map((chapter) => {
      return (chapter.offsetTop / totalHeight) * 100;
    });
    const episodesMarkers = episodesData.map(
      ({ index, title, subtitle, startEpisode }) => {
        return (
          <div
            key={"episodeMarker" + startEpisode}
            className="episodeMarker"
            style={{
              top:
                index === 0
                  ? chaptersTops[startEpisode] + 1 + "%"
                  : chaptersTops[startEpisode] - 1 + "%",
            }}
            onClick={(e) => this.episodeClicked(e, index, startEpisode)}
            onMouseOver={() =>
              this.setState({ episodeTitle: subtitle + ": " + title })
            }
            onMouseOut={() => this.setState({ episodeTitle: "" })}
          />
        );
      }
    );
    this.setState({
      chaptersTops: chaptersTops,
      episodesMarkers: episodesMarkers,
    });
  }

  componentDidUpdate(prevProps) {
    if (prevProps.searchIndices !== this.props.searchIndices) {
      this.setState({
        prevSearches: prevProps.searchIndices,
        currentSearches: [],
      });
      setTimeout(
        function () {
          this.setState({
            currentSearches: this.props.searchIndices,
          });
        }.bind(this),
        300
      );
    }
  }

  scrollBarClicked = (e) => {
    const heightFract = e.clientY / window.innerHeight;
    this.props.setScrollPos(heightFract * 100);
    this.props.setHeightFract(heightFract);
  };

  onHover = (e) => {
    const mousePos = (e.clientY / window.innerHeight) * 100;
    var hoverMouseId = -1;
    for (var i = 1; i < this.state.chaptersTops.length; i++) {
      if (
        mousePos > this.state.chaptersTops[i - 1] &&
        mousePos < this.state.chaptersTops[i]
      ) {
        hoverMouseId = i - 1;
        break;
      }
    }
    this.setState({
      hoverMouseId: hoverMouseId,
      mousePos: window.innerHeight - e.clientY,
    });
  };

  markerClicked = (e, id) => {
    e.stopPropagation();
    this.props.setActive(parseInt(id));
  };
  render() {
    const markers = this.state.currentSearches.map((i) => {
      return (
        <div
          key={"marker" + i}
          className="marker markerFadeIn"
          style={{ top: this.state.chaptersTops[i] + "%" }}
          onMouseEnter={() => this.props.setHover(parseInt(i))}
          onMouseOut={() => this.props.setHover(-1)}
          onClick={(e) => this.markerClicked(e, i)}
        />
      );
    });

    const prevMarkers = this.state.prevSearches.map((i) => {
      return (
        <div
          className="marker markerFadeOut"
          style={{ top: this.state.chaptersTops[i] + "%" }}
        />
      );
    });

    const infoContainer = this.props.infoState ? (
      <div
        className={"info infoScrollBar " + this.props.infoState}
        id="infoScrollBar"
      />
    ) : null;

    var hoverDate = "";
    const top =
      this.props.hoverId >= 0
        ? this.state.chaptersTops[this.props.hoverId]
        : -10;
    var mouseMarkerTop = -10;
    if (this.state.hoverMouseId >= 0) {
      const hoverChapter = chapters[this.state.hoverMouseId];
      hoverDate =
        months[parseInt(hoverChapter.date.month) - 1] +
        " " +
        hoverChapter.date.year;
      mouseMarkerTop = window.innerHeight - this.state.mousePos;
    }

    const colorChoice =
      mouseMarkerTop > (this.props.scrollPos / 100) * window.innerHeight;
    const mouseMarkerClass = this.state.episodeTitle
      ? "transparentMarker"
      : colorChoice
      ? "brightMarker"
      : "darkMarker";
    const mouseDateClass = this.state.episodeTitle
      ? "shineDate"
      : this.props.playlist
      ? "brightDate"
      : "darkDate";

    const styleParameter =
      this.state.mousePos > window.innerHeight * 0.45 ? "top" : "bottom";
    return (
      <div>
        {infoContainer}
        <div
          className="scrollBar"
          id="scrollBar"
          onClick={(e) => this.scrollBarClicked(e)}
          onMouseMove={(e) => this.onHover(e)}
          onMouseLeave={() => this.setState({ hoverMouseId: -1 })}
        >
          <div
            className="thumb"
            style={{ height: this.props.scrollPos + "%" }}
          />
          <div
            className="marker"
            id="active"
            style={{
              top: top + "%",
            }}
          />
          <div
            className="movingDateContainer"
            style={{
              bottom: this.state.hoverMouseId >= 0 ? this.state.mousePos : -10,
            }}
          >
            <div
              className={"movingDate " + mouseDateClass}
              style={{
                [styleParameter]: 0,
              }}
            >
              {this.state.episodeTitle ? this.state.episodeTitle : hoverDate}
            </div>
            <div className={"hoverMarker " + mouseMarkerClass} />
          </div>
          {prevMarkers}
          {markers}
          {this.state.episodesMarkers}
        </div>
      </div>
    );
  }
}

export default ScrollBar;
