import React from "react";
import "./GeoMap.scss";

import {
  ComposableMap,
  Geographies,
  Geography,
  ZoomableGroup,
  Marker,
  Line,
  Annotation,
} from "react-simple-maps";

const info = require("../../data/Defs.json").info;
const mapInfo = info.map;
const scrollBarInfo = info.scrollBar;

const geoUrl =
  "https://cdn.jsdelivr.net/npm/world-atlas@2/countries-110m.json";

const lines = require("../../data/lines.json").lines;
const countries = require("../../data/Defs.json").countries;
const chapters = require("../../data/chapters_new.json").chapters;

var linesCoords = lines.map(({ x, y }) => {
  return [y, x];
});

class GeoMap extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showLines: false,
      zoom: 1.1,
      center: [0, 0],
    };
  }

  buildClasses = (index) => {
    if (this.props.episodeBounds.length === 2) {
      return index >= this.props.episodeBounds[0] &&
        index < this.props.episodeBounds[1]
        ? "regularDot activeDot"
        : "regularDot transparent";
    }
    var markersClasses = "regularDot lowOpacity ";
    markersClasses += this.props.searchIndices.includes(index.toString())
      ? "searched "
      : "nonActiveDot ";
    if (index === this.props.hoverId || index === this.props.activeId) {
      markersClasses = "regularDot activeDot";
    }
    return markersClasses;
  };

  minusClicked = () => {
    const targetZoom = Math.max(1.1, this.state.zoom / 2);
    this.setState({ zoom: targetZoom });
    if (targetZoom === 1.1) {
      this.setState({ center: [0, 0] });
    }
  };

  render() {
    var annotation = null;
    if (this.props.line.length === 2) {
      const from = chapters[this.props.line[0]].place;
      const to = chapters[this.props.line[1]].place;
      annotation = [
        [from.y, from.x],
        [to.y, to.x],
      ];
    }
    const infoContainer = this.props.infoState ? (
      <div className={"info infoMap " + this.props.infoState} id="infoMap">
        <div className="infoBox infoBoxScrollBar">
          <div className="infoHeader">{scrollBarInfo.header}</div>
          <div className="infoDescription">{scrollBarInfo.text}</div>
        </div>
        <div className="infoBox infoBoxMap">
          <div className="infoHeader">{mapInfo.header}</div>
          <div className="infoDescription">{mapInfo.text}</div>
        </div>
      </div>
    ) : null;
    const linesDiv = this.state.showLines ? (
      <Line
        className={"line"}
        coordinates={linesCoords}
        strokeLinecap="round"
        strokeWidth={1.0 / this.state.zoom}
      />
    ) : null;
    const connectorDiv =
      this.props.line.length === 2 ? (
        <Line
          className={"connector"}
          coordinates={annotation}
          strokeLinecap="round"
          strokeWidth={3.0 / this.state.zoom}
        />
      ) : null;
    const annotationsDiv = this.state.showLines ? this.annotations() : null;
    const mapContainerClasses = this.state.showLines
      ? " mapContainerLines"
      : "";
    const geographyClasses = this.state.showLines
      ? "rsm-geography rsm-geography-lines"
      : "rsm-geography rsm-geography-normal";
    const mapLinesIconClasses = this.state.showLines
      ? "mapLinesActive"
      : "mapLinesNonActive";
    const width = 960,
      height = 540;

    return (
      <div>
        {infoContainer}
        <div className={"mapContainer" + mapContainerClasses}>
          <div
            className={mapLinesIconClasses + " infoContainer mapLinesIcon"}
            onClick={() => this.setState({ showLines: !this.state.showLines })}
          >
            <i
              className="fas fa-plane fa-lg"
              style={{ transform: "rotate(-30deg)" }}
            ></i>
          </div>
          <div
            className={
              "plusClass " + mapLinesIconClasses + " infoContainer mapLinesIcon"
            }
            onClick={() =>
              this.setState({ zoom: Math.min(8, this.state.zoom * 2) })
            }
          >
            <i className="fas fa-plus fa-sm"></i>
          </div>
          <div
            className={
              "minusClass " +
              mapLinesIconClasses +
              " infoContainer mapLinesIcon"
            }
            onClick={() => this.minusClicked()}
          >
            <i className="fas fa-minus fa-sm"></i>
          </div>
          <ComposableMap width={width} height={height} className="mapContent">
            <ZoomableGroup
              zoom={this.state.zoom}
              center={this.state.center}
              minZoom={1.1}
              onMoveEnd={(e) =>
                this.setState({ zoom: e.zoom, center: e.coordinates })
              }
              translateExtent={[
                [105, 0],
                [width - 50, height],
              ]}
            >
              <Geographies className={geographyClasses} geography={geoUrl}>
                {({ geographies }) =>
                  geographies.map((geo) => (
                    <Geography key={geo.rsmKey} geography={geo} />
                  ))
                }
              </Geographies>
              {linesDiv}
              {annotationsDiv}
              {chapters.map(({ index, place }) => (
                <Marker
                  key={"place" + index}
                  id={"place" + index}
                  coordinates={[place.y, place.x]}
                >
                  <g className={this.buildClasses(index)}>
                    <circle
                      cx="0"
                      cy="0"
                      r={
                        this.props.searchIndices.includes(index.toString())
                          ? 2 + 5 / this.state.zoom
                          : 1 + 5 / this.state.zoom
                      }
                      onMouseEnter={() => this.props.setHover(index)}
                      onMouseOut={() => this.props.setHover(-1)}
                      onClick={() => this.props.setActive(index)}
                    ></circle>
                  </g>
                </Marker>
              ))}
              {connectorDiv}
            </ZoomableGroup>
          </ComposableMap>
        </div>
      </div>
    );
  }

  annotations = () => {
    return countries.map(({ name, coords, dx, dy, curve, anchor, offset }) => {
      return (
        <Annotation
          subject={coords}
          dx={dx}
          dy={dy}
          curve={curve}
          connectorProps={{
            stroke: "#e84e26",
            strokeWidth: 0.5 + 0.5 / this.state.zoom,
            strokeLinecap: "round",
            strokeDasharray: "0.5 4",
          }}
        >
          <text
            x={offset}
            textAnchor={anchor}
            alignmentBaseline="middle"
            fontSize={3 + 10 / this.state.zoom + "pt"}
          >
            {name}
          </text>
        </Annotation>
      );
    });
  };
}

export default GeoMap;
