import React from "react";
import "./TextFrame.scss";

const info = require("../../data/Defs.json").info;
const textInfo = info.text;
const description = info.description;
const thanks = info.thanks;
const chapters = require("../../data/chapters_new.json").chapters;
const episodesData = require("../../data/episodes.json").episodes;
const episodesIndices = episodesData.map(({ startEpisode }) => {
  return startEpisode;
});
const episodes = episodesData.map(
  ({
    index,
    title,
    subtitle,
    startDate,
    endDate,
    totalEpisodes,
    totalWords,
    wordsPerDay,
    countries,
    artists,
    emotionsCount,
    popularSearches,
  }) => {
    return (
      <div className="episode" key={"episode" + index} id={"episode" + index}>
        <div className="episodeBanner">
          <div className="episodeHeader">
            <div className="episodeSubtitle">{subtitle}</div>
          </div>
          <div className="episodeHeader">
            <div className="episodeTitle">{title}</div>
          </div>
          <div className="episodeHeader">
            <div className="episodeDates">{startDate + " - " + endDate}</div>
          </div>
        </div>
        <div className="episodeDetails">
          <div className="rowDetails">
            {totalEpisodes +
              " פרקים, " +
              totalWords +
              " מילים, " +
              wordsPerDay +
              " מילים בממוצע ליום."}
          </div>
          <div className="rowHeader">{"מילים פופולריות:  "}</div>
          <div className="rowDetails">{popularSearches}</div>
          <div className="rowHeader">{"מדינות:  "}</div>
          <div className="rowDetails">{countries}</div>
          <div className="rowHeader">{"אמנים מובילים:  "}</div>
          <div className="rowDetails episodeArtists">{artists}</div>
          <div className="rowHeader">{"חלוקה רגשית:  "}</div>
          <div className="rowDetails">{emotionsCount}</div>
        </div>
      </div>
    );
  }
);

const stickyDivs = chapters.map(({ index, date, place, song, artist }) => {
  const dateString = date.day + "." + date.month + "." + date.year;
  const placeString = place.city + ", " + place.country;
  return (
    <div className="sticky" id={index}>
      <div className="details">
        <div>{dateString + " | " + placeString}</div>
        <div />
      </div>
      <div className="details">
        <div>{song + " | " + artist}</div>
        <div />
      </div>
    </div>
  );
});

class TextFrame extends React.Component {
  constructor(props) {
    super(props);
    this.containerRef = React.createRef();
    this.state = {
      chaptersTops: [],
    };
  }

  updateChapter = () => {
    var id = 0;
    for (var i = 1; i < this.state.chaptersTops.length; i++) {
      if (
        this.props.heightFract > this.state.chaptersTops[i - 1] &&
        this.props.heightFract < this.state.chaptersTops[i]
      ) {
        id = i - 1;
        break;
      }
    }
    this.props.setActive(id);
  };

  componentDidMount() {
    this.containerRef.current.addEventListener("scroll", this.setScrollPos);
    const chapters = Array.prototype.slice.call(
      document.getElementsByClassName("chapter")
    );
    const totalHeight = document.getElementById("chapter").offsetHeight;
    const chaptersTops = chapters.map((chapter) => {
      return chapter.offsetTop / totalHeight;
    });
    this.setState({
      chaptersTops: chaptersTops,
    });
  }

  getSentenceElement() {
    const targetSentenceIndex = chapters[this.props.hoverId].headerIndices[0];
    return "chapter" + this.props.hoverId + "sentence" + targetSentenceIndex;
  }

  componentDidUpdate(prevProps) {
    if (
      prevProps.activeId !== this.props.activeId &&
      prevProps.hoverId === this.props.activeId
    ) {
      const searchIndices = this.props.searchIndices[this.props.activeId];
      const targetString = searchIndices
        ? "chapter" + this.props.activeId + "sentence" + searchIndices[0]
        : this.getSentenceElement();
      const targetSentence = document.getElementById(targetString);
      targetSentence.scrollIntoView(true);
      document.getElementById("content").scrollTop -= 300;
      this.props.setScrollPos(this.state.chaptersTops[this.props.activeId]);
    }
    if (prevProps.heightFract !== this.props.heightFract) {
      this.containerRef.current.scrollTo({
        top:
          this.props.heightFract *
          document.getElementById("chapter").offsetHeight,
      });
      this.updateChapter();
    }

    if (prevProps.episode !== this.props.episode && this.props.episode > -1) {
      const targetString = "episode" + this.props.episode;
      const targetSentence = document.getElementById(targetString);
      targetSentence.scrollIntoView(true);
    }
  }

  setScrollPos = () => {
    const contentScroll = this.containerRef.current.scrollTop;
    const height = document.getElementById("chapter").offsetHeight;
    const heightFract = contentScroll / height;
    this.props.setScrollPos(heightFract * 100);

    const episodeIndex = episodesIndices.findIndex(
      (i) => i === this.props.activeId
    );
    if (episodeIndex > -1) {
      if (
        this.props.episode > -1 &&
        heightFract >
          this.state.chaptersTops[this.props.activeId] +
            window.innerHeight / height
      ) {
        this.props.setEpisode(-1);
      }
    }

    const moveToNextChapter =
      this.props.activeId < this.state.chaptersTops.length - 2 &&
      heightFract > this.state.chaptersTops[this.props.activeId + 1] &&
      heightFract < this.state.chaptersTops[this.props.activeId + 2];
    if (moveToNextChapter) {
      this.props.setActive(this.props.activeId + 1);
      return;
    }
    const moveToPreviousChapter =
      this.props.activeId > 0 &&
      heightFract > this.state.chaptersTops[this.props.activeId - 1] &&
      heightFract < this.state.chaptersTops[this.props.activeId];
    if (moveToPreviousChapter) {
      this.props.setActive(this.props.activeId - 1);
    }
  };

  render() {
    const textSpan = chapters.map(({ index, sentences, headerIndices }) => {
      const searchIndices = this.props.searchIndices[index];
      const sentencesDivs = sentences.map((sentence, j) => {
        var spanClass = "";
        if (searchIndices) {
          if (searchIndices.find((i) => parseInt(i) == j)) {
            spanClass += "searched";
          }
        }
        headerIndices.map((activeSentence) => {
          if (j === parseInt(activeSentence)) {
            spanClass += " active";
            if (index === this.props.activeId) {
              spanClass += " currentActive";
            }
          }
        });
        return (
          <span
            key={"chapter" + index + "sentence" + j}
            id={"chapter" + index + "sentence" + j}
            className={spanClass}
          >
            {sentence}
          </span>
        );
      });
      var episodeIndex = episodesIndices.findIndex((elem) => elem === index);
      const episodePage = episodeIndex > -1 ? episodes[episodeIndex] : null;
      return (
        <div key={"chapter" + index} id={"chapter" + index} className="chapter">
          {episodePage}
          {stickyDivs[index]}
          <div className="chapterContent">{sentencesDivs}</div>
        </div>
      );
    });

    const infoContainer = this.props.infoState ? (
      <div className={"infoText " + this.props.infoState} id="infoText">
        <div className="logo" />
        <div className="stickyContainer">
          <div className="infoSticky" />
        </div>
        <div className="desc">{description.text}</div>
        <div className="infoBox infoBoxDetails">
          <div className="infoHeader">{textInfo.header}</div>
          <div className="infoDescription">{textInfo.text}</div>
        </div>
        <div className="infoBox infoBoxThanks">
          <div className="infoHeader">{thanks.header}</div>
          <div className="infoDescription">{thanks.text1}</div>
          <div className="infoDescription">
            לאביחי שריקי וענבל יהב על בניית
            <a href="https://arxiv.org/abs/2102.01909"> המודל הרגשי </a>
            לשפה העברית.
          </div>
          <div className="infoDescription">{thanks.text3}</div>
        </div>
      </div>
    ) : null;

    const resizeClass = this.props.wideScreen ? "compress" : "expand";
    return (
      <div>
        {infoContainer}
        <div className="TextContainer">
          <div
            className={"resizeIcon " + resizeClass}
            onClick={() => this.props.setWideScreen()}
          />
          <div
            className="infoContainer iconContainer"
            onClick={() => this.props.setPlaylist(true)}
          >
            <i className="fab fa-itunes-note fa-lg" />
          </div>
          <div className="content" id="content" ref={this.containerRef}>
            <div key={"chapter"} id={"chapter"}>
              {textSpan}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default TextFrame;
