import React from "react";
import "./EmotionsMap.scss";

const chapters = require("../../data/chapters_new.json").chapters;
const defs = require("../../data/Defs.json");
var center = [0, 0];
var emotionsCount = 8;

defs.emotions.map(({ coord }) => {
  center[0] += coord[0];
  center[1] += coord[1];
});

const emotionInfo = defs.info.emotion;
const searchInfo = defs.info.search;
center = [center[0] / emotionsCount, center[1] / emotionsCount];

function produceEmotionsLabels() {
  return defs.emotions.map(({ type, coord, offset, hebrewLabel }) => {
    return (
      <text
        className="label"
        key={type}
        x={coord[0] + offset[0]}
        y={coord[1] + offset[1]}
      >
        {hebrewLabel}
      </text>
    );
  });
}

function calculatePosition(emotions) {
  var position = [center[0], center[1]];
  var counter = countNonZero(emotions);
  defs.emotions.find(({ type, coord }) => {
    const value = emotions.find((emotion) => emotion.type == type).value;
    position[0] += (value * (coord[0] - center[0])) / counter;
    position[1] += (value * (coord[1] - center[1])) / counter;
  });
  return position;
}

function countNonZero(emotions) {
  var counter = 0;
  emotions.map(({ value }) => {
    if (value > 0) {
      counter++;
    }
  });
  return counter;
}

const emotionsLabels = produceEmotionsLabels();
const dotsPositions = chapters.map(({ headerEmotions }) => {
  return calculatePosition(headerEmotions);
});

function EmotionsMap(props) {
  var annotation = [[], []];
  const chapterDots = chapters.map(({ index, wordsCount }) => {
    var circleClasses = "regularDot nonActiveDot lowOpacity";
    var searched = false;
    if (props.episodeBounds.length === 2) {
      circleClasses =
        index >= props.episodeBounds[0] && index < props.episodeBounds[1]
          ? "regularDot activeDot"
          : "regularDot transparent";
    } else {
      if (props.searchIndices.includes(index.toString())) {
        searched = true;
        circleClasses = "regularDot searched lowOpacity";
      }
      if (index === props.hoverId || index === props.activeId) {
        circleClasses = "regularDot activeDot";
      }
    }

    const position = dotsPositions[index];
    const lineIndex = props.line.findIndex((elem) => elem === index);
    if (lineIndex > -1) {
      annotation[lineIndex] = position;
    }
    const radius = searched ? 8 + 0.002 * wordsCount : 5 + 0.002 * wordsCount;

    return (
      <circle
        className={circleClasses}
        key={"dot" + index}
        id={"dot" + index}
        cx={position[0]}
        cy={position[1]}
        r={radius}
        onMouseEnter={() => props.setHover(index)}
        onMouseOut={() => props.setHover(-1)}
        onClick={() => props.setActive(index, "emotion")}
      />
    );
  });
  const infoContainer = props.infoState ? (
    <div className={"info " + props.infoState} id="infoEmotions">
      <div className="infoBox infoBoxEmotion">
        <div className="infoHeader">{emotionInfo.header}</div>
        <div className="infoDescription">{emotionInfo.text}</div>
      </div>

      <div className="infoBox infoBoxSearch">
        <div className="infoHeader">{searchInfo.header}</div>
        <div className="infoDescription">{searchInfo.text}</div>
      </div>
    </div>
  ) : null;
  const connector =
    props.line.length === 2
      ? "M" +
        annotation[0][0] +
        " " +
        annotation[0][1] +
        " Q" +
        (annotation[0][0] + annotation[1][0]) / 2 +
        " " +
        (annotation[0][1] + annotation[1][1] - annotation[0][1] + 0.01) +
        ", " +
        annotation[1][0] +
        " " +
        annotation[1][1]
      : "";
  return (
    <div>
      {infoContainer}
      <div className="emotionsMap">
        <svg
          className="emotionsContent"
          version="1.1"
          viewBox="0 100 1000 850"
          xmlns="http://www.w3.org/2000/svg"
          preserveAspectRatio="xMinYMin meet"
          height="100%"
        >
          <path
            className="diamond"
            d="M347.51,807.31,218.82,676.76A84.32,84.32,0,0,1,194.56,617l1.31-183.32A84.31,84.31,0,0,1,221,374.23L351.54,245.54a84.28,84.28,0,0,1,59.78-24.26l183.32,1.31a84.29,84.29,0,0,1,59.43,25.12L782.76,378.26A84.26,84.26,0,0,1,807,438l-1.31,183.31a84.29,84.29,0,0,1-25.12,59.44L650,809.48a84.3,84.3,0,0,1-59.78,24.26L407,832.43A84.34,84.34,0,0,1,347.51,807.31Z"
          />
          {emotionsLabels}
          {chapterDots}
          {connector !== "" ? (
            <path
              d={connector}
              className="connector"
              id="emotionsConnector"
              strokeWidth="3px"
            />
          ) : null}
        </svg>
      </div>
    </div>
  );
}

export default EmotionsMap;
